.App {
  text-align: center;
  background-color: #282c34;
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: start;
  font-size: calc(10px + 2vmin);
  color: white;
  padding: 20px;
}

.MessagesContainer {
  height: 80%;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
}

.Messages {
  flex: 1;
  overflow: auto;
}

.MessageTitle {
  width: 100%;
  text-align: flex-start;
}

.Message {
  width: 100%;
  text-align: flex-start;
  font-size: small;
}

.enterTrade {
  height: 20%;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
}

.TradeInputs {
  font-size: medium;
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  gap: 20px;
  align-items: center;
}

.KeyValue {
  flex-direction: column;
  display: flex;
  text-align: flex-start;
}

.tradeEnter {
  height: 26px;
}